export const alphabet = {
    'danish': /^[A-Z]+$/,
    'french': /^[A-Z]+$/,
    'german': /^[A-Z]+$/,
    'greek': /^[-]+$/,
    'spanish': /^[a-z]+$/i,
    'russian': /^[-]+$/,

};

export const alphaWithWhitespace = {
    'danish': /^[A-Z\s]+$/,
    'french': /^[A-Z\s]+$/,
    'german': /^[A-Z\s]+$/,
    'greek': /^[-\s]+$/,
    'spanish': /^[a-z\s]+$/i,
    'russian': /^[-\s]+$/,

};

export const alphanumeric = {
    'danish': /^[0-9A-Z]+$/,
    'french': /^[0-9A-Z]+$/,
    'german': /^[0-9A-Z]+$/,
    'spanish': /^[0-9a-z]+$/i,
    'russian': /^[0-9-]+$/,
};

export const alphanumericWithWitespace = {
    'danish': /^[0-9A-Z]+$/,
    'french': /^[0-9A-Z]+$/,
    'german': /^[0-9A-Z]+$/,
    'spanish': /^[0-9a-z\s]+$/i,
    'russian': /^[0-9-]+$/,
};

